.reach-us {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 70px;

  @media (min-width: @screen-l) {
    padding-bottom: 65px;
  }

  &__inner {

  }

  &__title {
    margin-right: 10%;
    margin-left: 10%;
    margin-bottom: 30px;
  }

  &__map {
    width: 100%;
    display: block;
    position: relative;

    @media (min-width: @screen-l) {
      .inner-screen-l();
    }

    &-mask {
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background-color: transparent;
    }
  }

  &__transport-type {
    padding: 0 40px;
    margin-top: 35px;

    @media (min-width: @screen-l) {
      .inner-screen-l();
      margin-top: 30px;
      display: flex;
      flex-direction: row;
    }
  }
}
